.header {
  position: sticky;
  left: 0;
  top: 0;
  z-index: 100;
  padding: 0.9375rem 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(10px);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
  }

  &__logo svg rect {
    transition: 0.3s ease-in-out;

    @include hover {
      fill: rgba($color: #000, $alpha: 1);
    }
  }

  &__nav {
    @include mobile {
      position: fixed;
      left: -100%;
      top: 0;
      z-index: 101;
      overflow-y: auto;
      width: 80%;
      height: 100vh;
      max-width: 23.4375rem;
      background-color: rgba(0, 0, 0, 0.6);
      transition: opacity 0.3s, visibility 0.3s, transform 0.3s, left 0.3s;
      backdrop-filter: blur(10px);
    }
  }
}

.nav {
  &__list {
    display: flex;
    align-items: center;
    gap: 30px;

    @include tablet {
      gap: 25px;
    }

    @include small-tablet {
      gap: 20px;
    }

    @include mobile {
      flex-direction: column;
      align-items: start;
      padding: 20px;
    }
  }

  &__item {
  }

  &__link {
    position: relative;
    color: var(--light-color);
    transition: all 0.2s ease-in-out;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -2px;
      border-radius: 4px;
      width: 100%;
      height: 1px;
      background-color: var(--light-color);
      transform: scaleX(0);
      transform-origin: right;
      transition: transform 0.3s ease-in-out;
    }

    @include hover {
      &::before {
        transform: scaleX(1);
        transform-origin: left;
      }
    }

    @include mobile {
      font-size: 36px;
    }
  }
}

.menu--active {
  left: 0;
}
