// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --content-width: 1210px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --black-color: #1c1c1c;
  --red-color: #f00;

  // font
  --h1-font-size: clamp(1.875rem, 1.6154rem + 1.1538vw, 3rem);
  --subtitle-font-size: clamp(1.5rem, 1.4135rem + 0.3846vw, 1.875rem);
  --section-title: clamp(1.875rem, 1.7885rem + 0.3846vw, 2.25rem);
  --h3-font-size: clamp(1.5rem, 1.3846rem + 0.5128vw, 2rem);
  --h4-font-size: clamp(1.25rem, 1.1923rem + 0.2564vw, 1.5rem);
  --text-font-size: clamp(0.875rem, 0.8462rem + 0.1282vw, 1rem);
  --button-font-size: clamp(1rem, 0.9423rem + 0.2564vw, 1.25rem);
}
