.projects {
  &__container {
  }

  &__title {
    margin-bottom: 20px;
  }

  &__wrapper {
    height: 100%;

    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));

    gap: 30px;
  }
}

.project {
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
  &__image {
    border-radius: 20px;
  }

  &__caption {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    gap: 10px;
  }

  &__title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    color: var(--light-color);
  }

  &__list {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__stack {
    flex-shrink: 0;
  }

  &__stack-item {
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--light-color);
  }

  &__text {
    margin: 0;

    flex-grow: 1;

    color: var(--light-color);
  }

  &__link {
    display: flex;
    align-items: center;
    gap: 5px;

    color: var(--light-color);

    transition: 0.3s ease;

    @include hover {
      color: var(--red-color);
    }
  }

  &__links {
    margin-top: auto;
    display: flex;
    gap: 10px;
  }

  &__link-code {
  }

  &__link-site {
  }
}
