.skills {
  &__container {
    display: flex;
    flex-direction: column;
    gap: 1.875rem;
  }

  &__title {
    margin: 0;
    color: var(--light-color);
  }

  &__frontend {
  }

  &__subtitle {
    color: var(--light-color);
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 1.25rem;
    gap: 0.9375rem 1.25rem;
  }

  &__item {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: center;
    border: 0.125rem solid var(--red-color);
    border-radius: 0.3125rem;
    padding: 0.625rem;
    min-width: 6.25rem;
    max-width: fit-content;
    text-align: center;
    color: var(--red-color);
    transition: 0.3s ease;
    cursor: pointer;
    gap: 0.3125rem;

    @include hover {
      box-shadow: 0 0 1.5625rem var(--red-color);
      color: var(--light-color);
      background-color: var(--red-color);
      transform: scale(1.03) translateY(-0.3125rem);

      svg {
        color: var(--light-color);
        fill: var(--light-color);
      }
    }
  }

  &__item svg {
    width: 16px;
    height: 16px;
    color: var(--red-color);
    fill: var(--red-color);
  }

  &__styling {
  }

  &__miscellaneous {
  }
}

