.about {
  &__container {
  }

  &__title {
    margin-bottom: 25px;
    color: var(--light-color);
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }

  &__article {
    display: flex;
    flex: 1;
    flex-direction: column;
    border-radius: 20px;
    padding: 20px;
    min-width: 290px;
    color: var(--light-color);
    background-color: var(--red-color);
    transition: background-color 0.3s ease, color 0.3s ease;
    cursor: pointer;
    gap: 25px;

    @include hover {
      color: var(--red-color);
      background-color: transparent;
    }
  }
}

.article {
  &__title {
    margin: 0;
  }

  &__text {
    margin: 0;
  }
}
