.hero {
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1.875rem;

    @include small-tablet {
      flex-direction: column;
    }

    @include mobile {
      gap: 15px;
    }
  }

  &__image {
    @include mobile {
      max-width: 230px;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    gap: 1.5625rem;

    @include mobile {
      gap: 15px;
    }
  }

  &__title {
    margin: 0;
    font-size: var(--h1-font-size);
    color: var(--light-color);
  }

  &__span {
    color: var(--red-color);
  }

  &__title-span {
  }

  &__subtitle {
    margin: 0;
    color: var(--light-color);
  }

  &__subtitle-span {
    overflow: hidden;
    border-right: 0.0625rem solid;
    white-space: nowrap;
  }

  &__description {
    margin: 0;
    color: var(--light-color);
  }

  &__social {
  }

  &__social,
  &__list {
    display: flex;
    align-items: center;
    gap: 1.875rem;
  }

  &__list {
    flex-wrap: wrap;
  }

  &__item {
  }

  &__link {
    display: inline-block;
    border: 0.125rem solid var(--red-color);
    border-radius: 4rem;
    padding: 1rem 2rem;
    color: var(--red-color);
    background: transparent;
    transition: 0.3s ease;
    cursor: pointer;

    @include hover {
      box-shadow: 0 0 1.5625rem var(--red-color);
      color: var(--light-color);
      background-color: var(--red-color);
      transform: scale(1.03);
    }
  }

  &__link-call {
  }

  &__scroll {
    display: none;

    @include for-desktop {
      display: flex;
      justify-content: center;
      padding: 40px 0;
    }
  }

  &__scroll-link {
    width: 100%;
    max-width: fit-content;
  }

  &__scroll-image {
    background-color: var(--red-color);
  }

  &__scroll-arrow {
    animation: moveUpDown 2s infinite ease-in-out;
  }
}

@keyframes moveUpDown {
  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

.social {
  &__item {
  }

  &__link {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 0.15rem solid var(--red-color);
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    font-size: 2rem;
    color: var(--red-color);
    background: transparent;
    transition: 0.3s ease;

    @include hover {
      box-shadow: 0 0 1.5625rem var(--red-color);
      color: var(--light-color);
      background-color: var(--red-color);
      transform: scale(1.3) translateY(-0.3125rem);
    }
  }
}
