.contact {
  padding: 30px 0;
  background-color: #000;
  &__container {
  }

  &__title {
  }

  &__span {
    color: var(--red-color);
  }

  &__form {
    margin: 20px auto 0;

    max-width: 800px;

    display: flex;
    flex-direction: column;
    gap: 20px;
  }
}

.form {
  &__label {
    display: flex;
    flex-direction: column;
    gap: 10px;

    color: var(--light-color);
  }

  &__input {
  }

  &__textarea {
    resize: vertical;
  }

  &__input,
  &__textarea {
    padding: 10px;

    border: 2px solid var(--red-color);
    border-radius: 10px;
    background-color: transparent;
    color: var(--light-color);

    transition: 0.3s ease;
  }

  &__input:focus-visible,
  &__textarea:focus-visible {
    outline: 0;
    box-shadow: 5px 5px 5px var(--red-color);
  }

  &__btn {
    margin: 0 auto;
    padding: 10px 20px;

    max-width: fit-content;

    color: var(--red-color);
    border: 1px solid var(--red-color);
    border-radius: 10px;

    transition: 0.3s ease;

    @include hover {
      font-weight: 700;
      box-shadow: 0 0 20px var(--red-color);
      color: var(--light-color);
      background-color: var(--red-color);
    }
  }
}
